*{
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

@font-face {
  font-family: DMSans-Regular;
  src: url("./fonts/DMSans/DMSans-Regular.ttf");
}


body{
  background-color: #2c2c2c;
  font-family: DMSans-Regular;
  color:#fff
}

.pricingTable{
  height:100vh;
  min-height: 580px;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around; 
}

.intro{
  height: 55vh;
  width: 100%;
  color:#fff;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
}

.img_title{
  height: auto;

  width: auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
}
.img_title img{
  margin-bottom: 24px;
  height: 70px;
}
.inputs{
  width: 390px;
  color:#fff;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 5px;
  /* margin-top: -70px; */
}
.inputs label{
  font-size: 14px;
  font-weight: bold;
}



#passingField, #rtoField{
  height: auto;
  flex-basis: 40%;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: space-around;

}

#rtoField select, #passingField select{
  
  height: 32px;
  width: 100%;
  background: none;
  border:1.5px solid rgb(141, 141, 141);
  border-radius: 4px;
  color:#fff;
  padding-left: 4px;
  font-size: 15px;
  font-weight: bold;
  margin-top: 5px;

}


#fetchData{
  border: 1px solid #d6d6d6;
  color: #fff;
  border-radius: 2px;
  background-color: #2c2c2c;
  height: 32px;
  width: 120px;
  font-weight: 600;
  cursor: pointer;
  margin-bottom: 90px;
}

.pricingList{
  height: auto;
  width: 390px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
  position: absolute;
  top:140px;
  /* background-color: #05e77e; */
}

#location{
  height: 32px;
  width: 100%;
  text-align: center;
  font-weight: 600;
  margin-top: -40px;
}

#gstTag{
  height: 32px;
  width: 100%;
  text-align: center;
  margin: 8px 0;
}

.accordion{
  margin-bottom:10px;
  width:390px;
  /* background-color: aqua; */
}


.price_field{
  height: auto;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin: 8px 0;
  padding: 4px 0;
}






@media screen and (max-width:400px) {
  .inputs{
    width: 320px;
  }
  .pricingList{
    width: 320px;
  }
}

@media screen and (max-width:320px){
  .inputs{
    width: 300px;
  }
  .pricingList{
    width: 300px;
  }
}

.footer{
  position: absolute;
  bottom:10px;
  left:50%;
  transform:translate(-50%, -50%);
  display: flex;
  align-items: center;
  justify-content: space-around;
  color:#fff;
  /* display: none; */
  font-size: 10px;
  z-index: -1;
}

/* .footer p:last-child{
  opacity: 70%;
} */

.footer img{
  height: 20px;
  width: auto;
  margin-right: 20px;
}


